import React, { Component } from 'react';
import { withRouter } from 'react-router-dom'
import style from './style.module.css'
import { connect } from 'react-redux'
// antd
import { Drawer, Modal, Button } from 'antd'
import { DeleteOutlined } from '@ant-design/icons';
// components
import Pay from '../../../../../Containers/AddACount/Pay/Pay';
// actions
import { getCountData } from '../../../../../redux/actions/countData';
import { deleteCount } from '../../../../../redux/actions/saveCount';
// reducer
import queryCountData from '../../../../../redux/reducers/countData';
import random from 'number-random';

class DaySteam extends Component {
  // timeSeg
  constructor(props) {
    super(props)
    this.type = this.props.history.location.params.type
    this.state = {
      type: this.props.history.location.params.type,
      // 
      list: queryCountData('', { type: 'getCountData', data: { list: 'all', demand: { account: this.type, date: [this.props.timeSeg.start, this.props.timeSeg.end] } } }),
      // 
      visible: false,
      // 
      selected: this.props.selected || [{ id: 0, pay: true }],
      // 
      isModalVisible: false,
      // idid
      flash: { id: void (0) },
      // 
      flag: false
    }
  }

  static getDerivedStateFromProps (nextProps, preState) {
    const { selected, flash } = nextProps
    // list
    if (preState.list.length === 0) {
      return { ...preState, visible: false }
    }
    // flashnull
    if (flash && !preState.flag) {
      return { ...preState, list: queryCountData('', { type: 'getCountData', data: { list: 'all', demand: { account: preState.type, date: [nextProps.timeSeg.start, nextProps.timeSeg.end] } } }), flag: true, visible: false }
    }
    // selected[0].id0,bugflash
    if (selected.length === 1 && selected[0].id !== 0) {
      return { ...preState, selected: nextProps.selected, flag: false }
    }
    return { ...preState, flag: false }
  }
  shouldComponentUpdate (nextProps, nextState) {
    if (nextProps.flash === null) {
      return true
    }
    if (nextProps.flash && (nextState.flag || nextState.isModalVisible)) {
      return true
    }
    return false;
  }

  countId = 0
  // 
  deleteCount = (e) => {
    e.stopPropagation();
    this.countId = e.target.parentNode.parentNode.parentNode.dataset.key
    if (this.countId === undefined) {
      return null
    } else {
      this.setState({ ...this.state, isModalVisible: true })
    }
  }
  // 
  handleOk = (e) => {
    e.stopPropagation();
    this.props.deleteCount(this.countId)
    this.setState({ ...this.state, isModalVisible: false })
  }
  // 
  handleCancel = (e) => {
    e.stopPropagation();
    this.setState({ ...this.state, isModalVisible: false })
  }
  isModalVisible = []
  // this.state.list
  renderList = () => {
    let keyArr = Object.keys(this.state.list)
    if (keyArr.length === 0) {
      return null
    }
    return (
      keyArr.map(item => {
        const { id, category, account, pay, count } = this.state.list[item]
        return (
          <div className={style.row} key={random(1, 10000, false)} data-key={id} >
            <div style={{ marginLeft: id ? '0' : '45%' }}>
              <span>{category ? category.split('>')[0] : '123'}</span>
              <span>{account}</span>
            </div>
            <div style={{ color: !pay ? '#e17167' : '#56b78c', display: id ? 'block' : 'none' }}>{count}</div>
            <div style={{ position: 'absolute', right: '25px', paddingTop: '14px', width: '20px', height: '20px' }} onClick={this.deleteCount}>
              <DeleteOutlined />
            </div>
          </div>
        )
      }))
  }
  // 
  editCount = (e) => {
    if (e.target.className === 'ant-modal-footer' || e.target.className === 'ant-modal-title' || e.target.className === 'ant-modal-body' || e.target.innerHTML === '') {
      e.stopPropagation()
      return null
    }
    let id
    if (e.target.dataset.key) {
      id = e.target.dataset.key
    } else {
      if (e.target.parentNode.dataset.key) {
        id = e.target.parentNode.dataset.key
      } else {
        id = e.target.parentNode.parentNode.dataset.key
      }
    }
    this.props.getCountData({ list: 'all', demand: { id: id } })
    this.onOpen()
  }
  // 
  onClose = (e) => {
    this.setState({ ...this.state, visible: false })
  }
  // 
  onOpen = (e) => {
    this.setState({ ...this.state, visible: true })
  }
  // 
  title = () => {
    return (
      <div className={style.head} style={{ display: 'flex', flexDirection: 'row-reverse', padding: 0, width: '700px' }}>
        <Button type="text" onClick={this.onClose} danger style={{ fontSize: '20px', color: 'brown', marginTop: '20px' }}>
          
        </Button>
        <span className={style.headname}> </span>
      </div>
    )
  }
  a = 0
  render () {
    this.a++
    return (
      <div>
        {/*  */}
        <div className={style.container}>
          <div className={style.card} onClick={this.editCount}>
            {this.renderList()}
          </div>
        </div>
        {/*  */}
        <Drawer
          title={this.title()}
          placement="right"
          closable={false}
          onClose={this.onClose}
          visible={this.state.visible}
          width={'700px'}
          headerStyle={{ margin: 0, padding: 0 }}
          className={style.drawerBody}
          destroyOnClose={true}
        >
          <div style={{ paddingTop: '80px', width: '700px', transform: 'translateX(0)', overflow: 'auto', height: '100%' }}>
            <Pay pay={this.state.selected.length ? this.state.selected[0].pay : true} count={this.state.selected}></Pay>
          </div>
        </Drawer>
        {/*  */}
        <Modal title="" visible={this.state.isModalVisible} onOk={this.handleOk} onCancel={this.handleCancel} width={300}>
          <p></p>
        </Modal>

      </div>
    )
  }
}
const mapStateToProps = (state) => {
  return {
    selected: state.countData,
    flash: state.saveCount
  }
}
export default connect(
  mapStateToProps,
  { getCountData, deleteCount }
)(withRouter(DaySteam));
